<template>
<div class="container">
  <div class="item">
    <div class="item__link"><router-link to="/jsonplaceholder/posts/" class="item__link-exit">&lt-- Назад</router-link></div>
    
    <div class="item__box" v-if="itemInfo">
        <div class="item__box-info">
         <h3>Пользователь №{{itemInfo.userId}}</h3>
       <p><span>Id его поста:</span> {{itemInfo.id}}</p>
       <p><span>Название его поста:</span> {{itemInfo.title}}</p>
       <p><span>Пост:</span> {{itemInfo.body}}</p>

    </div>
    </div>
    
  </div>
  </div>
</template>

<script>
    import { mapState } from "vuex";
export default {
    data(){
        return {
            itemInfo: null
        }
    },
    computed:{
        ...mapState(['allPosts'])
    },
    mounted(){
        this.itemInfo = this.allPosts.find((item)=>item.id==this.$route.params.id)
        
    }
}
</script>

